

import { Http, Response, Headers, RequestOptions } from '@angular/http';
import {MdSnackBar} from '@angular/material';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import { 
    Component,
    trigger,
    state,
    style,
    transition,
    animate,
    keyframes,
	ViewChild
 } from '@angular/core';

import { ReCaptchaComponent } from 'angular2-recaptcha';
 

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
    styleUrls: ['./app.component.css'], 
 animations: [
    trigger(
      'enterAnimation', [
        transition(':enter', [
          style({transform: 'translateY(100%)', opacity: 0}),
          animate('500ms', style({transform: 'translateY(0)', opacity: 1}))
        ]),
        transition(':leave', [
          style({transform: 'translateY(0)', opacity: 1}),
          animate('500ms', style({transform: 'translateY(100%)', opacity: 0}))
        ])
      ]
    )
  ],
})
export class AppComponent {
 services:ServiceInfo[];
 refnumber:string;
  model = new Register(1,'','','','','','','','','','','','','',"","","","",'',[]);
  state = 0;
  searchno = "";
  load = true;
  enqstatus = "";
  enqreview = "";
  enqmessage = "";
 genders:any;

 

  @ViewChild(ReCaptchaComponent) captcha: ReCaptchaComponent;



  constructor(private http: Http,public snackBar: MdSnackBar) { 
	this.genders = [];
     this.genders.push(new Gender('0','Male'));
	this.genders.push(new Gender('1','Female'));
   
  }


   uistate: string = 'inactive';

    toggleMove() {
        this.uistate = (this.uistate === 'inactive' ? 'active' : 'inactive');
    }

	openSnackBar(message: string, action: string) {
						this.snackBar.open(message, action, {
						  duration: 2000,
						});
						}

  search(){


  return	new Promise((resolve) => {
					this.http.get("/home/enquiry?refno=" + this.searchno).subscribe((data) => {
		
					if(data.json()) {
					
						resolve(data.json());
						var enq = data.json();
						if(enq.code == 400){
						this.openSnackBar("Invalid reference number.","ok");
						
					  }

						

						if(enq.code == 200){
							this.enqstatus = enq.status;
							this.enqreview = enq.review;
							this.enqmessage = enq.message;
						}
						

					}
					
					else{
						console.log("Error");
					}
				}
			)
		});
  }

  previousState(){
	let newstate = this.state - 1;
	if(newstate >= 0 && newstate != 3){
	this.goState(newstate);
	}
  }

  nextState(){
  let newstate = this.state + 1;
	if(newstate <= 2  && newstate != 3){
	this.goState(newstate);
	}
  }

  goHome(){
	this.goState(0);
  }

  goState(s){
	this.state = s;
	let rs =  this.getRequestServices();
	console.log(rs);

	
  }


  //console.log(services);

  ser = new Promise((resolve) => {
					this.http.get("/home/getservices").subscribe((data) => {
					  this.load = false;
					if(data.json()) {

						resolve(data.json());
						this.services = data.json();
						
						

					}else{
						console.log("Error");
					}
				}
			)
		});
  

  getRequestServices():any{

	let results : string[] = [];

	if(this.services)
	for(let t of this.services){
		for(let s of t.service){
			if(s.checked){
				results.push(s.code);
			}
		}
		

	}

	this.model.services = results;
	return results;
  }


  postUrl = '';



  postData(){
	if(!this.model.recaptcha){
	this.openSnackBar("Human verification fail.","ok");

	}
	else{
	this.load = true;
	var headers =  new Headers({ 'Content-Type': 'application/json' });
	headers.append('Access-Control-Allow-Origin', '*' );

	let options = new RequestOptions({ headers: headers });

	this.captcha.reset();
	return new Promise((resolve) => {
					this.http.post("/home/register", this.model, options).subscribe((data) => {
					this.load = false;
					if(data.json()) {

						resolve(data.json());
						var result = data.json();
						if(result.code == 200){
							this.state = 3;
							this.refnumber = result.message;
							
						}
						else{
							
							this.openSnackBar(result.message,"ok");
						}

					}else{
						console.log("Error");
					}
				}
			)
		});
		}
  
  }

  handleCorrectCaptcha(e){
  this.model.recaptcha = e;

  }

  imageUploaded(s){
	 console.log('uploaded..');
	  console.log(s.serverResponse._body);
	  let result = JSON.parse(s.serverResponse._body);
	  if(result.code = 200){
	  this.model.photo = result.message;
	  }
	  else{
	
		this.openSnackBar(result.message,"ok");
	  }
  }
  imageRemoved(s){
  this.model.photo = null;
   console.log('remove..');
   
  }
  disableSendButton(s){
   console.log(s);
  }

  nrcImageUploaded(s){
	 console.log('uploaded..');
	  console.log(s.serverResponse._body);

	  let result = JSON.parse(s.serverResponse._body);

	  if(result.code = 200){
	  this.model.nrc1 = result.message;
	  }
	  else{
		
		this.openSnackBar(result.message,"ok");
	  }
	  
	  
  }
  nrcImageRemoved(s){
   console.log('remove..');
   this.model.nrc1 = null;
    console.log(s);
  }
  nrcDisableSendButton(s){
   console.log(s);
  }


   nrcBackImageUploaded(s){
	 console.log('uploaded..');
	  console.log(s.serverResponse._body);

	  let result = JSON.parse(s.serverResponse._body);

	  if(result.code = 200){
	  this.model.nrc2 = result.message;
	  }
	  else{
	
		this.openSnackBar(result.message,"ok");
	  }
	  
  }
  nrcBackImageRemoved(s){
   console.log('remove..');
   this.model.nrc2 = null;
    console.log(s);
  }
  nrcBackDisableSendButton(s){
   console.log(s);
  }


   sigBackImageUploaded(s){
	 console.log('uploaded..');
	  console.log(s.serverResponse._body);

	  let result = JSON.parse(s.serverResponse._body);

	  if(result.code = 200){
	  this.model.signature = result.message;
	  }
	  else{
	
		this.openSnackBar(result.message,"ok");
	  }
	  
  }
  sigBackImageRemoved(s){
   console.log('remove..');
   this.model.signature = null;
    console.log(s);
  }
  sigBackDisableSendButton(s){
   console.log(s);
  }
}


export class Register {

  constructor(
    public id: number,
    public fname: string,
	public lname: string,
	public dob: string,
	public mobile: string,
	public email: string,
	public city: string,
    public state: string,
	public address: string,
    public postal: string,
	public gender:string,
	public fathername:string,
	public nrcno:string,
	public salutation:string,
	public photo: string,
	public nrc1: string,
	public nrc2: string,
	public signature: string,
	public recaptcha :string,
	public services : string[]

  ) {  }
}

export class Service{
	constructor(
	
		public id:number,
		public name:string,
		public code:string,
		public type_id:number,
		public priority:number,
		public checked:boolean, 
	){}
}

export class Gender{
constructor(
		public value:string,
		public text:string,
	){}
}

export class ServiceInfo{
	constructor(
		public name:string,
		public order:number,
		public service:Service[],
	){}
}


